import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFTextField } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
WebInt.propTypes = {
  discount: PropTypes.object,
};
export default function WebInt({ discount }) {
  const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
  });
  const defaultValues = useMemo(
    () => ({
      Habukhan_website1: discount?.Habukhan_website1,
      Habukhan_website2: discount?.Habukhan_website2,
      Habukhan_website3: discount?.Habukhan_website3,
      Habukhan_website4: discount?.Habukhan_website4,
      Habukhan_website5: discount?.Habukhan_website5,

      virus_website1: discount?.virus_website1,
      virus_website2: discount?.virus_website2,
      virus_website3: discount?.virus_website3,
      virus_website4: discount?.virus_website4,
      virus_website5: discount?.virus_website5,

      msorg_website1: discount?.msorg_website1,
      msorg_website2: discount?.msorg_website2,
      msorg_website3: discount?.msorg_website3,
      msorg_website4: discount?.msorg_website4,
      msorg_website5: discount?.msorg_website5,
      boltnet_url: discount?.boltnet_url
    }),
    [discount]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount !== undefined) {
      reset(defaultValues);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]);
  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/edit/webapi/charges/account/${AccessToken}/habukhan/secure`, data)
      swal('success', `Updated`, 'success');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>

            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFTextField name="Habukhan_website1" label="Habukhan WEBSITE 1 (URL)" />
              <RHFTextField name="Habukhan_website2" label="Habukhan WEBSITE 2 (URL)" />
              <RHFTextField name="Habukhan_website3" label="Habukhan WEBSITE 3 (URL)" />
              <RHFTextField name="Habukhan_website4" label="Habukhan WEBSITE 4 (URL)" />
              <RHFTextField name="Habukhan_website5" label="Habukhan WEBSITE 5 (URL)" />
              {/* ms */}
              <RHFTextField name="msorg_website1" label="MSORG 1 (URL)" />
              <RHFTextField name="msorg_website2" label="MSORG 2 (URL)" />
              <RHFTextField name="msorg_website3" label="MSORG 3 (URL)" />
              <RHFTextField name="msorg_website4" label="MSORG 4 (URL)" />
              <RHFTextField name="msorg_website5" label="MSORG 5 (URL)" />

              <RHFTextField name="virus_website1" label="VIRUS 1 (URL)" />
              <RHFTextField name="virus_website2" label="VIRUS 2 (URL)" />
              <RHFTextField name="virus_website3" label="VIRUS 3 (URL)" />
              <RHFTextField name="virus_website4" label="VIRUS 4 (URL)" />
              <RHFTextField name="virus_website5" label="VIRUS 5 (URL)" />
              <RHFTextField name="boltnet_url" label="BoltNet (URL)" />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
